@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
}

.wrapper {
  width: 100%;
  height: 100%;
}


.table-header {
  display: flex;
  width: 100%;
  height: 50px;
}

.navbar {
  width: 100%;
  height: 100%;
  /* background-color: red; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding-right: 10px; */
  padding: 0 10px;
}

.table-wrapper {
  width: 100%;
  overflow: auto;
  max-width: 1500px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
  margin: 0 auto;
}

.nav-item {
  height: 40px;
  width: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
  cursor: pointer;
}

.icon-wrapper {
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 35;
  width: 35px;
  margin-right: 10px;
}

.nav-wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}



.table-body {
  width: 100%;
  height: 60px;
  display: flex;
}

.normalize {
  font-size: 14px;
}

.mantine-Modal-close {
  background-color: red;
  color: #fff;
}

.mantine-Modal-close:hover {
  background-color: red;
  opacity: 0.8;
}

.body-item {
  display: flex;
  /* width: 20%; */
  height: 100%;
  flex-shrink: 0;
  min-width: 180px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  align-items: center;
  justify-content: center;
  background-color: #fff;
  overflow: hidden;
}

.body-item:nth-of-type(1) {
  width: 28%;
  min-width: 300px;
}

.body-item:nth-of-type(2) {
  width: 18%;
  min-width: 200px;
}

.body-item:nth-of-type(3) {
  width: 18%;
