}

.header__item:nth-of-type(4) {
  width: 18%;
  min-width: 200px;
}

.header__item:nth-of-type(5) {
  width: 18%;
  min-width: 200px;
}



.header__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
  min-width: 180px;
  background-color: #fff;
